body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ColorBox {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    cursor: pointer;
}

.tall-row td {   height: 5%; width: 5%; border: 1px solid}
.page {
  height: 100vh;
  position: fixed;
  width: 100%;
  top: 0;
  transition: opacity 0.5s ease-in-out;
}
.page-enter {
  opacity: 0;
}
.page-enter-active {
  opacity: 1;
}
.page-exit-active {
  opacity: 0;
}

.form {
    height: 100vh;
    position: fixed;
    width: 100%;
    top: 0;
    transition: opacity 0.5s ease-in-out;
  }
